.nav-small {
  position: fixed;
  top: 0;
  left: 0;
  width: 0%;
  height: 100%;
  background: #000;
  z-index: 1000;
  opacity: 0;
  -webkit-transform-origin: right;
  transform-origin: right;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  font-size: 3rem;
}

.nav-small__list {
  list-style: none;
